<template>
    <div class="personal-content-body">
        <div class="content">
            <div class="content-title clear">
                <div class="left">文章详情页</div>
            </div>
            <div class="list-contaienr">
                <div class="list-header">共{{params.total}}篇文章</div>
                <div class="list-nav">
                    <span @click="goBack('tuneUpAssistantList')">尽调助手&nbsp;&nbsp;></span>
                    <span @click="goBack('jdzsHomepageZcsd',page)">政策速递&nbsp;&nbsp;></span>
                    <span>文章详情页</span>
                </div>
                <div class="list-line"></div>
                <div class="list-body">
                    <!-- <div class="article-name">{{detailsInfo.title}}</div>
              <div class="article-publish-time">发布时间：{{detailsInfo.issue_time}} 来源：{{detailsInfo.source}}</div> -->
                    <div class="article-name">{{detailsInfo.title}}</div>
                    <div class="article-publish-time">发布时间：{{detailsInfo.time}} <span class="source-style">{{detailsInfo.source}}</span></div>
                    <div>
                        <div class="article-title-icon"></div>
                    </div>
                    <div class="article-content" v-html="detailsInfo.content">
                    </div>
                </div>
                <div class="list-footer">
                    <button @click="getArticleDetails(detailsInfo.pre,1)" >上一篇</button>
                    <button @click="getArticleDetails(detailsInfo.next,2)">下一篇</button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {
        newsInfo
    } from "../../config/api";
    import {
        dialogConfirm,
        dialogConfirmHasbtn
    } from '../../config/mUtils'
    // import router from '../../router/router';
    export default {
        data() {
            return {
                detailsInfo: {},
                selectTime: null,
                keyWord: null,
                type: null,
                downId: null,
                upId: null,
                params: {},
                page: 1,
            };
        },
        mounted() {
            this.params = this.$route.query;
            if (this.$route.query.page) {
                this.page = this.$route.query.page;
            }
            this.getArticleDetails(this.params.id);
        },
        methods: {
            //获取详情信息
            getArticleDetails(detailId,type) {
                if (detailId == null||detailId=='') {
                    if(type==1){
                        dialogConfirm('info', '已经是第一篇');
                    }else{
                        dialogConfirm('info', '已经是最后一篇');
                    }
                    return false;
                }
                let params = {
                    news_id: detailId,
                    keywords:this.params.keywords
                    // type: this.params.type,
                };
                //   if (this.params.searchType == 'time') {
                //     params.issueTime = this.selectTime
                //   } else if (this.params.searchType == 'keyWord') {
                //     params.search = this.keyWord;
                //   }
                // console.log(params)
                
                newsInfo(params).then(res => {
                    // console.log(res);
                    if (res.data.msgNo == 1) {
                        this.detailsInfo = res.data.payload;
                    }
                });
            },
            goBack(name,page) {
                let params = {}
                if(page){
                    params.page = page
                }
                this.$router.push({
                    name: name,
                    query:params
                });
            }
        }
    };
</script>

<style lang="scss" scoped>
    .content {
        width: 100%;
        min-height: 100%;
        background-color: white;
        padding: 40px;
        padding-top: 30px;
    }
    
    .hidden {
        display: none;
    }
    
    .content-title {
        //   margin-bottom: 18px;
    }
    
    .content-title>div.left {
        font-size: 18px;
        line-height: 30px;
        color: #35383a;
        //   margin-bottom: 29px;
        font-weight: bold;
    }
    
    .header-right-time {
        // width: 115px;
        margin-right: 10px; // position: relative;
        // top: 4px;
    }
    
    .content-header button {
        border: 1px solid #f4f4f4;
        border-radius: 2px;
        padding: 1px 10px;
        font-size: 14px;
        color: #808080;
        outline: none;
        margin-left: 24px;
    }
    
    .content-header button:hover {
        border: 1px solid #f4f4f4;
        color: #4a90e2;
        background-color: rgba(#488cdc, 0.1);
        cursor: pointer;
    }
    
    .content-header button span {
        display: inline-block;
        width: 15px;
        height: 15px;
        position: relative;
        top: 2px;
        background-size: 15px;
    }
    
    .rl-btn {
        margin-right: 10px;
    }
    
    .list-contaienr {
        border: 1px solid #e4e4e4;
        padding: 20px 17px;
        position: relative;
    }
    
    .list-header {
        text-align: left;
        color: #3c98e8;
        font-size: 18px;
        font-weight: bold;
        margin-bottom: 13px;
    }
    
    .list-nav {
        // padding-left: 14px;
        margin-bottom: 12px;
    }
    
    .list-nav span {
        font-size: 14px;
        color: #3c98e8;
        cursor: pointer;
    }
    
    .list-nav span:last-child {
        color: #35383a;
        cursor: inherit;
    }
    
    .list-line {
        border-bottom: 1px solid #f4f4f4;
    }
    
    .list-body {
        width: 100%;
        padding: 17px 13px;
        overflow: auto;
        height: calc(100% - 55px);
    }
    
    .list-body .article-name {
        text-align: center;
        font-size: 22px;
        color: #35383a;
        font-weight: bold;
    }
    
    .list-body .article-publish-time {
        text-align: center;
        font-size: 14px;
        color: #a0a4aa;
        margin-top: 6px;
    }
    
    .list-body .article-content {
        font-size: 14px;
        color: #35383a;
        line-height: 24pt;
        margin-top: 8px;
    }
    
    .list-body .article-title-icon {
        display: inline-block;
        width: 20px;
        height: 4px;
        background-color: #3c98e8;
    }
    
    .list-footer {
        text-align: center;
        margin-top: 6px;
        margin-bottom: 30px;
    }
    
    .list-footer button {
        width: 74px;
        height: 28px;
        border: 1px solid #e3eaef;
        color: #a0a4aa;
        font-size: 14px;
        margin: 0 6px;
    }
    
    .nocursor {
        cursor: none;
    }
    
    .source-style {
        color: #1787fb;
        background-color: rgba(23, 135, 251, 0.1);
        font-size: 12px;
        padding: 2px 4px;
        margin-left: 20px;
    }
</style>



